import ArticleList from '../ArticleList';
import React from 'react';
import agent from '../../agent';
import { connect } from 'react-redux';
import { CHANGE_TAB } from '../../constants/actionTypes';
import Slider from 'react-slick';
import ArticlePreview from '../ArticlePreview';

const YourFeedTab = props => {
  if (props.token) {
    const clickHandler = ev => {
      ev.preventDefault();
      props.onTabClick('feed', agent.Articles.feed, agent.Articles.feed());
    }

    return (
      <li className="nav-item">
        <a  href=""
            className={ props.tab === 'feed' ? 'nav-link active' : 'nav-link' }
            onClick={clickHandler}>
          Your Feed
        </a>
      </li>
    );
  }
  return null;
};

const GlobalFeedTab = props => {
  const clickHandler = ev => {
    ev.preventDefault();
    props.onTabClick('all', agent.Articles.all, agent.Articles.all());
  };
  return (
    <li className="nav-item">
      <a
        href=""
        className={ props.tab === 'all' ? 'nav-link active' : 'nav-link' }
        onClick={clickHandler}>
        Global Feed
      </a>
    </li>
  );
};

const TagFilterTab = props => {
  if (!props.tag) {
    return null;
  }

  return (
    <li className="nav-item">
      <a href="" className="nav-link active">
        <i className="ion-pound"></i> {props.tag}
      </a>
    </li>
  );
};

const mapStateToProps = state => ({
  ...state.articleList,
  tags: state.home.tags,
  token: state.common.token
});

const mapDispatchToProps = dispatch => ({
  onTabClick: (tab, pager, payload) => dispatch({ type: CHANGE_TAB, tab, pager, payload })
});

const MainView = props => {
  var category = 'Events';
  var location = 'near you';
  var params = props.params.search && props.params.search.split('&') || [];
  if ( params && params.length > 0) {
    category = params[2].split('=')[1]
    if (category == '')
      category = 'Events';
    if (params[0] != 'location=')
      location = 'in ' + params[0].replace('location=', '').split('-')[0];
  }

  var settings = {
      arrows: false,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            infinite: true,
          }
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: true,
          }
        }
      ]
    };

  var deviceType = props.deviceType;

  var articles = props.articles;

  var listing = params.length == 0 && !props.params.tag && !props.params.askey && props.local;
  return (
    <>
      { listing?
        <>
          <div className="popular-events-container">
            <div className="location-label">
              { params.length == 0 && !props.params.tag && props.local && (
                  <>
                    Popular events in 
                    <div className="location-value">
                      { props.curCountry  && ('in ' + props.curCountry) }
                    </div>
                  </>
              )}      
              { params.length > 0 && (
                  <>
                    Popular { category } 
                    <div className="location-value">{ location }</div>
                  </>
              )}
            </div>

            {/* props.params.tag && (
                <h2>Events by { props.params.tag }</h2>
            )*/}

            <Slider {...settings}>
              {(deviceType !== 'tablet' && deviceType !== 'mobile') && <div className='slider-one'>
                {
                  articles && articles.slice(0, 2).map((article, index) => {
                    return (
                      <ArticlePreview 
                        key={index} 
                        article={article}
                      />
                    )
                  })
                }
              </div>}

              {(deviceType === 'tablet' || deviceType === 'mobile') && 
                <ArticlePreview 
                  article={articles.slice(0, 1)}
                />
              }

              {(deviceType === 'tablet' || deviceType === 'mobile') && 
                <ArticlePreview 
                  article={articles.slice(1, 2)}
                />
              }

              <ArticlePreview 
                className="slider-two"
                article={articles.slice(2, 3)}
              />

              <ArticlePreview 
                className="slider-three"
                article={articles.slice(3, 4)}
              />  

            </Slider>
          </div>
          <div className='mobile-download-container'>
            <div className='mobile-img'>
              <img src={require('../../assets/images/iPhone-X.png')} alt='iPhone-X' />
            </div>

            <div className='download-section'>
              <div className='download-header'>
                <div>Lorem lpsum</div>
                <div>is simply dummy text of the printing and typesetting industry.</div>
              </div>

              <div className='download-btns'>
                <div className='download-btn-googleplay'>
                  <img src={require('../../assets/images/googleplay.png')} alt='Google Play' />
                </div>

                <div className='download-btn-appstore'>
                  <img src={require('../../assets/images/appstore.png')} alt='App Store' />
                </div>
              </div>
            </div>
          </div>
          <div className='featured-events-container'>
            <div className='featured-events-title'>
              Featured events
            </div>

            <div className='events-content'>
              {
                props.featuredArticles.map((article, index) => {
                  return (
                    <ArticlePreview 
                      key={index} 
                      article={article}
                    />
                  )
                })
              }
            </div>

            {/*<div className='more-events'>
                More events...
              </div>*/}
          </div>
        </>
        :
        <>
          <div className="popular-events-container">
            <div className="location-label">
              { params.length == 0 && !props.params.tag && props.local && (
                  <>
                    Popular events in 
                    <div className="location-value">
                      { props.curCountry  && ('in ' + props.curCountry) }
                    </div>
                  </>
              )}      
              { params.length > 0 && (
                  <>
                    Popular { category } 
                    <div className="location-value">{ location }</div>
                  </>
              )}
            </div>
          </div>
          <div className='featured-events-container'>
            <div className='events-content'>
              {
                props.articles.map((article, index) => {
                  return (
                    <ArticlePreview 
                      key={index} 
                      article={article}
                    />
                  )
                })
              }
            </div>
          </div>
        </>
      }

    </>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(MainView);
