class Header extends React.Component {
  constructor() {
    super();
    this.state = {
      askey : '',
      customStylesForSelect: {}
    }
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleResize.bind(this));
    this.handleResize();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize.bind(this));
  }

  handleResize() {
    const { deviceType } = this.state

    if (window.innerWidth > 1440) {
      if (deviceType !== 'desktop-l') {
        this.setState({ customStylesForSelect: {
          container: (provided, state) => (
            {
              ...provided,
              border: 'none',
            }
          ),
          control: (provided, state) => (
            {
              ...provided,
              fontSize: 16,
              border: 'none',
              outline: 'none',
              minHeight: 20
            }
          ),
          indicatorSeparator: (provided, state) => (
            {
              ...provided,
              display: 'none'
            }
          ),
          input: (provided, state) => (
            {
              ...provided,
              fontSize: 16,
            }
          ),
          option: (provided, state) => (
            {
              ...provided,
              fontSize: 16,
            }
          ),
          singleValue: (provided, state) => (
            {
              ...provided,
              fontSize: 16,
            }
          ),
        }})

        this.setState({ deviceType: 'desktop-l' })
      }
    } else if (window.innerWidth > 768) {
      if (deviceType !== 'tablet') {
        this.setState({ customStylesForSelect: {
          container: (provided, state) => (
            {
              ...provided,
              border: 'none',
            }
          ),
          control: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
              border: 'none',
              outline: 'none',
              minHeight: 20
            }
          ),
          indicatorSeparator: (provided, state) => (
            {
              ...provided,
              display: 'none'
            }
          ),
          input: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
            }
          ),
          option: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
            }
          ),
          singleValue: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
            }
          ),
        }})

        this.setState({ deviceType: 'tablet' })
      }
    } else if (window.innerWidth > 320) {
      if (deviceType !== 'mobile') {
        this.setState({ customStylesForSelect: {
          container: (provided, state) => (
            {
              ...provided,
              border: 'none',
            }
          ),
          control: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
              border: 'none',
              outline: 'none',
              minHeight: 20
            }
          ),
          indicatorSeparator: (provided, state) => (
            {
              ...provided,
              display: 'none'
            }
          ),
          input: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
            }
          ),
          option: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
            }
          ),
          singleValue: (provided, state) => (
            {
              ...provided,
              fontSize: 14,
            }
          ),
        }})

        this.setState({ deviceType: 'mobile' })
      }
    }
  }

  render() {
    const { customStylesForSelect } = this.state

    return (
      <nav className="navbar">
        <div className="nav-left">
          <Link to="/" className="navbar-brand">
            {/*this.props.appName.toLowerCase()*/}
            <img src={ logoImg } />
          </Link>
        </div>

        <div className="search__block">
          <input
            className="form-control advanced-search"
            type="text"
            placeholder="Search events"
            value={this.state.askey}
            onChange={ (e) => this.setState({ askey : e.target.value }) }
            onKeyPress={ ev => this.props.advancedSearch(ev, this.state.askey) }
          />
          <div className="search__btn">
            <i className="fa fa-search" aria-hidden="true"></i>
          </div>
        </div>

        <LoggedOutView
          currentUser={this.props.currentUser}
          customStylesForSelect={customStylesForSelect}
        />

        <LoggedInView currentUser={this.props.currentUser}
          onClickLogout={this.props.onClickLogout}
          onClickSettting={ () => this.props.history.push('/settings/info')}
          customStylesForSelect={customStylesForSelect}
        />
      </nav>
    );
  }
}

export default withRouter(Header);
