import { Link } from 'react-router-dom';
import ListErrors from './ListErrors';
import React from 'react';
import agent from '../agent';
import { connect } from 'react-redux';
import {
  UPDATE_FIELD_AUTH,
  LOGIN,
  LOGIN_PAGE_UNLOADED
} from '../constants/actionTypes';
import $ from 'jquery';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';

const mapStateToProps = state => ({ 
  ...state.auth,
  currentUser : state.common.currentUser
});

const mapDispatchToProps = dispatch => ({
  onChangeEmail: value =>
    dispatch({ type: UPDATE_FIELD_AUTH, key: 'email', value }),
  onChangePassword: value =>
    dispatch({ type: UPDATE_FIELD_AUTH, key: 'password', value }),
  onSubmit: (payload, callback) =>
    dispatch({ type: LOGIN, payload, callback}),
  onUnload: () =>
    dispatch({ type: LOGIN_PAGE_UNLOADED })
});

class Login extends React.Component {
  constructor() {
    super();

    var _self = this;

    this.changeEmail = ev => this.props.onChangeEmail(ev.target.value);
    this.changePassword = ev => this.props.onChangePassword(ev.target.value);
    this.submitForm = (email, password) => ev => {
      ev.preventDefault();
      var payload = agent.Auth.login(email, password);
      payload.then(res => {        
        if (res.errors){
          _self.props.history.push({
            pathname: '/email-confirmation',
            state : { email : email }
          });
        }
        else {
          if(_self.props.location.state && _self.props.location.state.callback)
            _self.props.onSubmit(payload, _self.props.location.state.callback);
          else
            _self.props.onSubmit(payload);
        }
      }).catch( err => {
        _self.props.onSubmit(payload);
      })  
    };

    this.responseFacebook = response => {
      if (response.id){
        const payload = agent.Auth.facebookAuth(response);
        if(this.props.location.state && this.props.location.state.callback)
          this.props.onSubmit(payload, this.props.location.state.callback);
        else
          this.props.onSubmit(payload);
        this.props.onSubmit(payload);
      }
    }
  }

  componentWillMount() {
    $("html, body").animate({ scrollTop: 0 }, 0);
  }

  componentWillUnmount() {
    this.props.onUnload();
  }

  render() {
    if(this.props.currentUser)
      this.props.history.push({
        hash : '#ticket-info',
        pathname: this.props.location.state && this.props.location.state.callback ? this.props.location.state.callback : '/',
        state: this.props.location.state
      });

    const email = this.props.email;
    const password = this.props.password;
    return (
      <div className="auth-page login">
        <div className="container page">
          <div className="row">
            <div className="col-md-12 col-xs-12">
              <h1 className="auth-sub-header1">
                Let's get started
              </h1>
              <p className="text-xs-center auth-sub-header2">
                Use Facebook or email to get started
              </p>
              <ListErrors errors={this.props.errors} />
              <form onSubmit={this.submitForm(email, password)}>
                <fieldset>

                  <fieldset className="form-group">
                    {/*<i className="fa fa-user-o" aria-hidden="true"></i>*/}
                    <input
                      className="form-control"
                      type="email"
                      placeholder="Email"
                      value={email}
                      onChange={this.changeEmail} />
                  </fieldset>

                  <fieldset className="form-group">
                    {/*<i className="fa fa-keyboard-o" aria-hidden="true"></i>*/}
                    <input
                      className="form-control"
                      type="password"
                      placeholder="Password"
                      value={password}
                      onChange={this.changePassword} />
                  </fieldset>
                  <fieldset className="form-group">
                    <button
                      className="btn form-control"
                      type="submit"
                      disabled={this.props.inProgress}>
                      Sign in
                    </button>
                  </fieldset>

                </fieldset>
              </form>

              <p className="separator">or</p>

              <fieldset>
                <FacebookLogin
                  appId="2136840116623024"
                  callback={this.responseFacebook}
                  render={renderProps => (
                    <button onClick={renderProps.onClick} className="btn facebook">
                      <i className="fa fa-facebook-square" aria-hidden="true"></i>
                      <span>Continue with Facebook</span>
                    </button>
                  )}
                />
              </fieldset>

              <p className="text-xs-center auth-sub-header3">
                <Link to="/register/user">
                  Need an account?
                </Link>
              </p>
              
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);
